












































.tagset {
  margin: 2rem auto;
}

.tagset ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.tagset li {
  margin: .2rem;
}

.tagset li a {
  display: block;
  padding: .2rem 1rem;
  margin: 0;
  border: 1px solid #d2d2d2;
  border-radius: 1.5rem;
  color: #f7f7f7;
  background-color: rgb(196, 196, 196);
  -webkit-transition: all .4s;
  transition: all .4s;
}

.tagset li a:hover, .tagset .tagset-active {
  background-color: #efefef;
  color: #424242;
}



